
.project-item {
  margin-right: 20px;
  width: 186px;
  height: 78px;
  border-radius: 5px;
  font-size: 18px;
  color: #FFFFFF;
  line-height: 78px;
  text-align: center;
}
.project-item h3 {
    font-weight: normal;
    margin: 0;
}
.project-component {
  margin-right: 40px;
  width: 270px;
  border: 1px solid #F3F3F3;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  position: relative;
}
.project-component .language {
    height: 3px;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
}
.project-component h3 {
    font-size: 18px;
    color: #717171;
    font-weight: normal;
    margin: 12px 0;
    width: 100%;
    text-align: center;
}
.project-component .private {
    border: 1px solid #F1F1F1;
    border-radius: 2px;
    font-size: 12px;
    color: #A8A7A7;
    width: 50px;
    height: 18px;
    line-height: 18px;
    text-align: center;
}
.project-component .repo a {
    font-size: 12px;
    color: #A8A7A7;
}
.project-component .repo a:hover {
      color: #848383;
}
.project-component .divider {
    height: 1px;
    background-color: #F3F3F3;
    width: 100%;
    margin-top: 15px;
}
.project-component .desc {
    padding: 0 12px;
}
.project-component .desc p {
      font-size: 12px;
      color: #9B9B9B;
}



